    letter-spacing: 20px;
  }

  .heading-primary-sub {
    font-size: 15px;
    font-weight: 400;
    letter-spacing: 8px;
  }
}



* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.contact {
  height: 100vh;
  background-color: #fff;
  display: flex;
  align-items: center;
}

.contact .contact-info {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.contact .contact-info .card {
  position: relative;
  flex: 1 1;
  max-width: 300px;
  height: 140px;
  background-color: #000;
  margin: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card .icon {
  font-size: 32px;
  color:#2ecc71; 
  transition: 0.3s linear;
}

.contact-info .card:hover .icon{
  transform: scale(4);
  opacity: 0;
}

.card-content h3,
.card-content span {
  position: absolute;
  opacity: 0;
  left: 50%;
  transform: translateX(-50%);
  font-size: 16px;
}

.card-content h3 {
  top: 20px;
  text-transform: uppercase;
  color: #2ecc71;
}

.card-content span {
  bottom: 20px;
  color: #fff;
  font-weight: 300;
}

.card:hover h3 {
  opacity: 1;
  bottom: 46px;
  transition: .3s linear .3s;
}

.card:hover span {
  opacity: 1;
  bottom: 46px;
  transition: .3s linear .3s;
}

@media screen and (max-width:900px) {
  .contact .contact-info .card {
    flex: 100% 1;
    max-width: 500px;
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.projects{
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  overflow: hidden;
}

 .card{
  display: grid;
  grid-template-columns: 300px;
  grid-template-rows: 210px 210px 80px;
  grid-template-areas: "image" "text" "links";

  border-radius: 18px;
  background: #fff;
  box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2);
  text-align: center;

  transition: 0.5s ease;
  cursor: pointer;
}

.card-image {
  grid-area: image;
  background: url(/portfolio/static/media/js.8680c54e.png);
  border-top-left-radius: 15px;
  border-top-right-radius:15px;
  background-size: cover;
}

.card-text {
  grid-area: text;
  margin: 25px;
}

.card-text h2 {
  margin-top: 0px;
  font-size: 28px;
}

.card-text p{
  color: grey;
  font-size: 15px;
  font-weight: 300;
}

.card-links {
  grid-area: links;
  display: grid;
  grid-template-columns: 1fr 1fr; 
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  background: #2ecc71;
}

.card-links .git,
.card-links .visit {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 10px;
  color: #fff;
  font-size: 18px;
}



.projects a {
  color: #fff;
}

.card-links .git a .icon {
  color: #fff;
}

.card-links .git a .icon:hover {
  transform: scale(1.2);
}

.card-links .border{
  border-left: 1px solid #27ae60;
}


/* .card:hover {
  transform: scale(1.2);
  box-shadow: 5px 5px 15px rgba(0,0,0,0.6)
} */

.projects a:hover {
  transform: scale(1.1);
}
