*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  text-decoration: none;
}

.nav {
  background: #fff;
  height: 65px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
}

.logo {
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  height: 50px;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  gap: 10px;
  list-style: none;
  text-align: center;
  width: 80vw;
  justify-content: end;
}

.navlinks {
  color: #000;
  text-decoration: none;
  padding: 0.5rem 1rem;
}

.navlinks:hover {
  color: #2ecc71;
  transition: all 0.1s ease-out;
}

.menu-icon {
  display: none;
}

@media screen and (max-width:900px) {
  .nav {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 65px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }
  
  .menu-icon {
    display: block;
    position: absolute;
    top: 8px;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #000; 
  }

  .nav-menu.active {
    background: #fff;
    left: 0;
    opacity: 0.8;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .navlinks {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
    color: #000;
  }

  .navlinks:hover {
    
    border-radius: 10px;
    color: #2ecc71;
  }

  .logo{
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(10%, 20%);
    cursor: pointer;
  }
} 
